<template>
  
</template>

<script>
  export default {
    data () {
      return {
        hour:24,
        // minute:00,
        // second:00
      }
    },
    mounted: function() {

      var interval = 1000; 
      function ShowCountDown(year,month,day,divname) 
      { 
      var now = new Date(); 
      var endDate = new Date(year, month-1, day); 
      var leftTime=endDate.getTime()-now.getTime(); //leftTime:
      console.log('leftTime',leftTime);
      var leftsecond = parseInt(leftTime/1000); 
      var day1=Math.floor(leftsecond/(60*60*24)); 
      var hour=Math.floor((leftsecond-day1*24*60*60)/3600); 
      var minute=Math.floor((leftsecond-day1*24*60*60-hour*3600)/60); 
      var second=Math.floor(leftsecond-day1*24*60*60-hour*3600-minute*60); 
      var cc = document.getElementById(divname); 
      cc.innerHTML = ""+year+""+month+""+day+""+day1+""+hour+""+minute+""+second+""; 
      } 
      window.setInterval(function(){ShowCountDown(2010,4,20,'divdown1');}, interval); 

        // console.log('this.hour',this.hour);
        // var hour=Math.floor(((this.hour)*60*60)/3600); 
        // var minute=Math.floor((leftsecond-day1*24*60*60-hour*3600)/60); 
        // var second=Math.floor(leftsecond-day1*24*60*60-hour*3600-minute*60); 
        // var cc = document.getElementById(divname); 
        // cc.innerHTML = year+""+month+""+day+""+day1+""+hour+""+minute+""+second+""; 
        // window.setInterval(function(){ShowCountDown(2010,4,20,'divdown1');}, 1000);
    },

  }

</script>

<style scoped>
  .text {
    background-color: #000000;
    color: #ffffff;
    padding: 3px 2px;
    border-radius: 2px;
  }
</style>


