
<style scoped>
#One{
    background-color: #f6f6f6;
}
.font{
    font-size: 1rem
}
.searchBar{
    padding: 0.33rem 0.2rem;
    display: flex;
    justify-content: space-around;
    background-color: #fff;
}
.searchBar input{
    flex: 0.9;
    height: 1rem;
    text-align: center;
    background: #eee;
    font-size: 0.34rem;
}
.searchBar i{
    flex: 0.1;
    font-size: 0.66rem;
    text-align: center;
}
.navBar{
    padding: 0.4rem 0;
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    justify-content: space-around;
}
.linkIcon{
    flex: 0.2;
    text-align: center;
}
.linkIcon span{
    display: inline-block;
    width: 1.17rem;
    height: 1.17rem;
    border-radius: 1.17rem;
    overflow: hidden;
}
.linkIcon span img{
    width: 100%;
    height: auto;
}
.linkIcon i{
    font-size: 0.4rem;
    color: #333
}
.linkIcon:active i{
    color: #fc3e32;
}
.saleSection{
    margin-top: 0.27rem;
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    justify-content:space-around;
}
.flashSale{
    flex: 0.3;
    padding: 0.53rem 0 1.13rem 0.4rem;
    border-right: 1px solid #f0f0f0;
}
.flashSale h1{
    font-family: '';
    font-size: 0.57rem;
    color: #333;
}
.flashSale p{
    margin-top: 0.3rem;
    font-size:0.37rem;
    color: #999; 
}
.hot{
    flex: 0.7;
}
</style>

<template>
    <div id="One">
        <div class="searchBar">
            <input v-model="value" class="iconfont" placeholder="" />
            <i class="iconfont">&#xe649;</i>
        </div>
        <slide></slide>
        <div class="navBar">
            <template v-for="tab in tabs">
                <a :href="tab.path">
                    <p class="linkIcon">
                        <span><img :src="tab.url"></span><br/>
                        <i>{{ tab.name }}</i>
                    </p>
                </a>
            </template>
        </div>
        <div class="saleSection">
            <div class="flashSale">
                <h1></h1>
                <p></p>
                <endtime id="divdown1"></endtime>
            </div>
            <div class="hot">

            </div>
        </div>
    </div>
</template>

<script>
    import slide from '../../component/slider.vue';
    import endtime from '../../pages/index/endTime.vue';
    export default {
        data() {
            return {
                author: "  ",
                value: '',
                tabs:[
                    {name:'',url:'/src/component/img/icon-1.jpg', path: '/a'},
                    {name:'',url:'/src/component/img/icon-2.jpg', path: '/b'},
                    {name:'',url:'/src/component/img/icon-3.jpg', path: '/c'},
                    {name:'',url:'/src/component/img/icon-4.jpg', path: '/d'},
                    {name:'',url:'/src/component/img/icon-5.jpg', path: '/e'}
                ],
            }
        },
        components:{
            slide,
            endtime
        },
        methods: {
            
        }
    }
</script>






